@mixin responsive($smallerValue,$biggerValue) {
  @media (min-width: $smallerValue) and (max-width: $biggerValue) {
    @content;
  }
}


.grid {
	.wrapper {
		width:95%;
		margin:0 auto;
		font-size:0;
		.coll {
			display:inline-block;
			vertical-align:top;
			font-size:20px;
			width: 25%;
			@include responsive(0,900px) {
				width: 50%;
				margin: 0;
			}
			@include responsive(0,600px) {
				width: 100%;
				height: 300px;
			}
			article {
				height:293px;
				display:block;
				display: block;
				color: #fff;
				line-height: 1.5;
				text-align: center;
				h1 {
					text-transform: uppercase;
					padding: 80% 25% 0 25%;
					font-size:24px;
					font-weight:bold;
					@include responsive(0,600px) {
						width: 100%;
						padding-top: 25%;
					}
				}
				h2 {
					font-size:24px;
					font-weight:bold;
					text-transform: uppercase;
					padding-top: 10%;	
					padding: 20% 25% 0 25%;
				}
				h3 {
					font-size:18px;
					font-family: georgia;
					font-style: italic;
				}
				h4 {
					font-size:14px;
					font-family: georgia;
					font-style: italic;
					padding-top: 10%;
				}
				p {
					font-size:14px;
					font-family: georgia;
					font-style: italic;
					padding:15% 15%;
					span {
						font-size:14px;
						font-weight:bold;
						font-style: italic;
					}
				}
				
			}
			 &:nth-child(1){
	          article{
	            &:nth-child(1){
	              background:#d2d2d2;
	              height:586px;
	            }
	            &:nth-child(2){
	              background:#c2c2c2;
	              height:293px;
	            }
	          }
	        }
	         &:nth-child(2){
	          article{
	            &:nth-child(1){
	              background:#bbb;
	              height:293px;
	            }
	            &:nth-child(2){
	              background:#cacaca;
	              height:293px;
	            }
	            &:nth-child(3){
	              background:#606060;
	              height:293px;
	            }
	          }
	        }
	        &:nth-child(3){
	          article{
	            &:nth-child(1){
	              background:#dbdbdb;
	              height:293px;
	            }
	            &:nth-child(2){
	              background:#bbb;
	              height:293px;
	            }
	            &:nth-child(3){
	              background:#d0d0d0;
	              height:293px;
	            }
	            @include responsive(0,900px) {
				display:none;
			}
	          }
	        }	
	        &:nth-child(4){
	          	article{
		            &:nth-child(1){
		              background:#606060;
		              height:293px;
		            }
		          	&:nth-child(2){
		              background:#d7d7d7;
		              height:586px;
		            }
		            @include responsive(0,900px) {
				display:none;
			}
	            }  
	        }	
		}
	}
}